    width: 1em;
    color: currentColor; }
  .lists ul li > ol:first-child,
  .lists ul li > ul:first-child,
  .lists ol li > ol:first-child,
  .lists ol li > ul:first-child {
    margin-top: 0.25em; }
  .lists ul li > ol:last-child,
  .lists ul li > ul:last-child,
  .lists ol li > ol:last-child,
